.container {
  display: block;
  width: calc(100% - 40px);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: calc(100% - 60px);
  }
}
@media (min-width: 1220px) {
  .container {
    width: calc(100% - 220px);
    max-width: 1220px;
    position: relative;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
