.button {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  border: none;
  background-color: #68b738;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: #ffffff30;
}

.checkbox {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}
@media (min-width: 768px) {
  .checkbox {
    grid-template-columns: 1fr 1fr;
    gap: 32px 55px;
  }
}
@media (min-width: 1220px) {
  .checkbox {
    grid-template-columns: repeat(4, 1fr);
    gap: 32px 80px;
  }
}

.checkbox__item {
  display: flex;
}

.checkbox__label {
  position: relative;
  padding: 6px 0;
}

.checkbox__text {
  display: block;
  margin: 0 0 0 45px;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #000000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .checkbox__text {
    font-size: 20px;
    line-height: 30px;
    margin: 0 0 0 60px;
  }
}

.checkbox__mark {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 0;
  left: 0;
  border: 1px solid #cdcdcd;
  cursor: pointer;
}
@media (min-width: 768px) {
  .checkbox__mark {
    width: 40px;
    height: 40px;
  }
}

.checkbox__input:checked + .checkbox__mark::after {
  position: absolute;
  content: "";
  width: 18px;
  height: 14px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("../img/icons/sprite.svg#checkbox");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .checkbox__input:checked + .checkbox__mark::after {
    width: 24px;
    height: 19px;
  }
}

.checkbox__input:focus + .checkbox__mark {
  outline: none;
  border: 1px solid #444444;
}

.checkbox__input:hover + .checkbox__mark {
  border-color: #bababa;
}

.checkbox__input:active + .checkbox__mark,
.checkbox__input:active ~ .checkbox__text {
  opacity: 0.3;
}

.checkbox__input:disabled + .checkbox__mark,
.checkbox__input:disabled ~ .checkbox__text {
  opacity: 0.3;
  cursor: default;
}

