.main-nav__toggle {
  z-index: 2;
  position: absolute;
  width: 64px;
  height: 64px;
  right: 0;
  top: 0;
  display: block;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
@media (min-width: 768px) {
